import React from "react";
import PptxGenJS from "pptxgenjs";

const PPT = () => {
  const save = () => {
    let pptx = new PptxGenJS();
    pptx.setTitle("Hello world Title");
    pptx.setLayout({ name: "A3", width: 16.5, height: 11.7 });
    let slide = pptx.addNewSlide("MASTER", { bkgd: "black", color: "white" });
    slide.addText("Hello", {
      x: 0.5,
      y: 0.7,
      w: 3,
      color: "white",
      fontSize: 64
    });
    slide.addText("Hello", {
      x: 2.7,
      y: 1.0,
      w: 5,
      color: "white",
      fontSize: 90
    });
    slide.pptx.save(`가사모음_20190619`);
  };
  return <button>download ppt</button>;
};

export default PPT;
